.sfarm-interface .bank {
  background: var(--color-card-bg);
  padding: 2rem;
  border-radius: 1.5rem;
  display: grid;
  grid-template-columns: minmax(20rem, 40%) 1fr;
  grid-template-rows: -webkit-max-content -webkit-max-content;
  grid-template-rows: max-content max-content;
  grid-gap: 2rem;
  gap: 2rem;
}
@media only screen and (max-width: 768px) {
  .sfarm-interface .bank {
    grid-template-columns: 1fr;
    grid-template-rows: auto 20rem auto;
  }
}
@media only screen and (max-width: 768px) {
  .sfarm-interface .bank .harvest {
    margin-bottom: 0;
    grid-row: 1/1 span;
  }
}
.sfarm-interface .bank .info {
  flex: 1;
}
@media only screen and (max-width: 768px) {
  .sfarm-interface .bank .info {
    grid-row: 3/1 span;
  }
}
.sfarm-interface .bank .info .info-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}
.sfarm-interface .bank .info .info-row > span {
  font-size: 1.3rem;
}
.sfarm-interface .bank .chart {
  grid-column: 2/1 span;
  grid-row: 1/2 span;
  width: 100%;
  height: 100%;
  color: var(--color-white);
  display: flex;
  justify-content: center;
  align-items: center;
  max-height: 20rem;
  min-width: 20rem;
  position: relative;
}
@media only screen and (max-width: 768px) {
  .sfarm-interface .bank .chart {
    grid-column: inherit;
    grid-row: 2/1 span;
  }
}
.sfarm-interface .bank .chart .chart-content {
  width: 100% !important;
  height: 100% !important;
  position: absolute;
  bottom: 0;
  left: 0;
}
