.sfarm-input-group {
  position: relative;
  display: table;
  border-collapse: separate;
}
.sfarm-input-group .input {
  position: relative;
  z-index: 2;
  float: left;
  width: 100%;
  display: table-cell;
  padding: 0 1rem;
  border: none;
  margin: 0;
  border-top-left-radius: 1.5rem;
  border-bottom-left-radius: 1.5rem;
  background: var(--color-input-bg);
  height: 26px;
  color: var(--color-text-default);
  font-size: 1.4rem;
  vertical-align: middle;
}
.sfarm-input-group .input::-moz-placeholder {
  /* Internet Explorer 10-11 */
  color: var(--color-grey-light);
  font-weight: 300;
}
.sfarm-input-group .input:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: var(--color-grey-light);
  font-weight: 300;
}
.sfarm-input-group .input::placeholder {
  /* Internet Explorer 10-11 */
  color: var(--color-grey-light);
  font-weight: 300;
}
.sfarm-input-group .input:focus {
  outline: none;
}
.sfarm-input-group .suffix {
  height: 26px;
  padding: 0 1rem;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
  text-align: center;
  border: none;
  width: 1%;
  white-space: nowrap;
  vertical-align: middle;
  display: table-cell;
  background: var(--color-input-bg);
  border-top-right-radius: 1.5rem;
  border-bottom-right-radius: 1.5rem;
}

.sfarm-select {
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  border-radius: 1.5rem;
  background: var(--color-input-bg);
}
.sfarm-select select {
  color: var(--color-grey-light);
  border: none;
  height: 26px;
  font-size: 1.4rem;
  display: table-cell;
  vertical-align: middle;
  background: transparent;
}
.sfarm-select select::-ms-expand {
  margin-right: 10px;
}
.sfarm-select select option {
  background: var(--color-input-bg);
}
.sfarm-select select:focus-visible {
  outline: none;
}
