.sfarm-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  opacity: 0;
  visibility: hidden;
  transition: all 0.25s ease-out;
}
.sfarm-modal.show {
  visibility: visible;
  outline: none;
  text-decoration: none;
  font-size: 70%;
  color: #000;
  opacity: 1;
}
.sfarm-modal .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background: var(--color-overlay);
}
.sfarm-modal .modal {
  background: var(--color-modal-bg);
  border-radius: 20px;
  width: 36rem;
  max-height: 100%;
  overflow: auto;
  z-index: 1001;
  padding: 1rem 1.5rem 2rem 1.5rem;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  margin-top: 18vh;
}
.sfarm-modal .modal .btn-close-wrap {
  text-align: right;
  margin-bottom: 0.5rem;
}
.sfarm-modal .modal .btn-close-wrap .btn-close {
  cursor: pointer;
}
