.sfarm-loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.sfarm-loading .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 100;
  background: var(--color-overlay);
}
.sfarm-loading .loading {
  z-index: 200;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}